<template>
<div >
  <el-card  :style="{display:'flex','justify-content':'center',}" >
<!--    table-start-->
    <el-table

      border
      ref="systemTable"
      :data="tableData"
      tooltip-effect="dark"
      :row-style="{'height':'10px'}"
      :cell-style="{'padding':'5px 0px'}"
      style="width:100%;_height: 100%;"
      class=""
      :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB','padding':'0px','height':'45px','line-height':'45px'}"
    >
      <el-table-column

        prop="sd_uuid"
        label="uuid"
        align="center"
        show-overflow-tooltip
        width="120"
        v-if="false"
      >

      </el-table-column>
      <el-table-column

        prop="sd_company_uuid"
        label="热力公司ID"
        align="center"
        show-overflow-tooltip
        width="120"
        v-if="false"
      >

      </el-table-column>

      <el-table-column

        prop="sd_memo"
        label="名称"
        align="center"
        show-overflow-tooltip
        width="150"

      >

      </el-table-column>
      <el-table-column

        prop="sd_value"
        label="值"
        align="center"
        show-overflow-tooltip
        width="200"

      >
        <template slot-scope="scope">
          <el-tag  effect="dark" size="small" v-if="scope.row.sd_value===undefined"  type="info">未配置</el-tag>
          <el-tag  effect="dark" size="small" v-else  type="success">{{ scope.row.sd_value }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column

        prop="sd_key"
        label="键"
        align="center"
        show-overflow-tooltip
        width="120"
        v-if="false"
      >
      </el-table-column>
        <el-table-column

          prop="sd_memo_2"
          label="备注"
          align="center"
          show-overflow-tooltip
          width="200"

        >
      </el-table-column>
      <el-table-column

        prop="sd_order"
        label="排序"
        align="center"
        show-overflow-tooltip
        width="120"

      >
      </el-table-column>
      <el-table-column
        label="操作"
        width="150">
        <template slot-scope="scope">
          <el-tooltip effect="dark" content="修改" placement="top">
            <el-button
              type="primary" icon="el-icon-edit" circle
              size="mini"
              @click.native.stop="handleEditRow(scope.$index, scope.row)"></el-button>
          </el-tooltip>
          <el-tooltip effect="dark" content="删除" placement="top">
            <el-button
              v-show="scope.row.sd_uuid===undefined?false:true"
              type="danger" icon="el-icon-delete" circle
              size="mini"
              @click.native.stop="handleDeleteRow(scope.$index, scope.row)"></el-button>
          </el-tooltip>

        </template>
      </el-table-column>

    </el-table>
<!--    table-end-->
    <!--page start-->

  <el-pagination
    background
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
    :current-page="queryParam.currentPage"
    :page-sizes="[10, 20, 50, 100,200]"
    :page-size="queryParam.pageSize"
    layout="total, sizes, prev, pager, next, jumper"
    :total="queryParam.totalRow">
  </el-pagination>

  </el-card>

    <!--page end-->


<!--  dialog start-->
  <el-dialog :visible.sync="configDialog.configFormVisible1"
             :close-on-press-escape="false"
             :close-on-click-modal="false"
             :show-close="false"

  >


    <div slot="title"  style="text-align: left;font-size:20px;font-weight: bold;">
      <span  >{{ this.configDialog.configDialogTitle}}</span>
    </div>
    <el-form  label-position="left" :model="configForm" :rules="configFormRules" ref="configForm" label-width="130px"   style="text-align: left;font-size:20px;font-weight: bold;">
      <el-form-item v-show="false" label="UUID" prop="sd_uuid"  :rules="this.configFormRules.sd_uuid">
        <el-input v-model="configForm.sd_uuid"></el-input>
      </el-form-item>
      <el-form-item v-show="false" label="组织机构" prop="sd_company_uuid"  :rules="this.configFormRules.sd_company_uuid">
        <el-input v-model="configForm.sd_company_uuid"></el-input>
      </el-form-item>
      <el-form-item   label="名称" >
        <el-input v-model="configForm.sd_memo" :disabled="true" :readonly="true"></el-input>
      </el-form-item>
      <el-form-item  v-show="false"  label="键" prop="sd_key"  :rules="this.configFormRules.sd_key">
        <el-input v-model="configForm.sd_key"></el-input>
      </el-form-item>


      <el-form-item  label="值" prop="sd_value"  :rules="this.configFormRules.sd_value">
             <el-row :gutter="24" type="flex" justify="space-between">
               <el-col  :md="configForm.sd_key==='logo_img'?16:24">
                 <el-input v-model="configForm.sd_value" :disabled="configForm.sd_key==='logo_img'?true:false"  :readonly="configForm.sd_key==='logo_img'?true:false"></el-input>
               </el-col>
               <el-col  :md="configForm.sd_key==='logo_img'?4:0">
                 <el-avatar  v-if="configForm.sd_key==='logo_img'" shape="square" :src="uploadConfig.uploadImageUrl" fit="fill" style="width:40px;height:40px;">
                   <img :src="nullImg" />
                 </el-avatar>
               </el-col>
               <el-col  :md="configForm.sd_key==='logo_img'?4:0">
                 <el-upload
                   ref="uploadFile"
                   :action="this.uploadConfig.actionUrl"
                   :with-credentials='true'
                   :show-file-list="true"
                   :limit="1"
                   accept=".png,.jpg"
                   :on-success="handleAvatarSuccess"
                   :before-upload="beforeAvatarUpload"
                   :on-exceed="selUploadFile"
                   >
                   <el-button _size="medium" type="success"  v-if="configForm.sd_key==='logo_img'" >上传</el-button>
                 </el-upload>

               </el-col>
             </el-row>
      </el-form-item>

      <el-form-item  label="备注" prop="sd_memo_2"  :rules="this.configFormRules.sd_memo_2">
        <el-input v-model="configForm.sd_memo_2"></el-input>
      </el-form-item>
      <el-form-item  label="排序" prop="sd_order"  :rules="this.configFormRules.sd_order">
        <el-input v-model="configForm.sd_order"></el-input>
      </el-form-item>


    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('configForm')">重 置</el-button>
<!--      <el-button @click="configDialog.configFormVisible1 = false">取 消</el-button>-->
      <el-button @click="closeDialog1">取 消</el-button>
      <el-button type="primary" @click="submitForm('configForm')">确 定</el-button>
    </div>
  </el-dialog>
<!--  dialog end-->

</div>
</template>

<script>
export default {
  name: "SystemConfigManager",
  data(){
    let validateUUID = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('唯一ID不可为空'));
      } else {
        callback();
      }
    };

    let validateCompanyID = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('组织机构ID不可为空'));
      } else {
        callback();
      }
    };

    let validateKey = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('键不可为空'));
      } else {
        callback();
      }
    };
    let validateValue = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('值不可为空'));
      } else {
        callback();
      }
    };

    let validateMemo = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('备注不可为空'));
      } else {
        callback();
      }
    };
    let validateOrder = (rule, value, callback) => {
      if (value.trim().length === 0 || this.customGlobal.isnumber(value)==false) {
        callback(new Error('排序不可为空,且必须是整数'));
      } else {
        callback();
      }
    };



    return{
      nullImg : require("@/assets/images/null.png"),

      uploadConfig:{
        actionUrl : this.customGlobal.serverUrl.orgBaseURI+"/fr/postCallPowerFileUpload?userid="+this.customGlobal.userInfoExt.cui_login_id+"&logic=2",
        uploadImgFlag:false,
        uploadPercent:0,
        uploadImageUrl:''
      },

      configForm:{
        sd_uuid:'',
        sd_company_uuid:'',
        sd_key:'',
        sd_value:'',
        sd_memo:'',
        sd_memo_2:'',
        sd_order:''

      },
      configFormRules:{
        sd_uuid:[
          {validator: validateUUID, trigger:'blur'}
        ],
        sd_company_uuid:[
          {validator: validateCompanyID, trigger:'blur'}
        ],
        sd_key:[
          {validator: validateKey, trigger:'blur'}
        ],
        sd_value:[
          {validator: validateValue, trigger:'blur'}
        ],
        sd_memo_2:[
          {validator: validateMemo, trigger:'blur'}
        ],
        sd_order:[
          {validator: validateOrder, trigger:'blur'}
        ],
      },
      configDialog:{
        configFormVisible1:false,
        configDialogTitle:'',
      },

      tableData:[],
      queryParam:{
        totalRow:0,
        currentPage:1,
        pageSize:this.customGlobal.pageSize,
      },
    }
  },
  created() {
    this.querySystemConfigData();

  },
  mounted() {

  },
  methods:{
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    submitForm(formName) {
      var that = this;
      console.info("窗体元素："+JSON.stringify( this.configForm) );
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.info("valid:"+valid);
          that.saveSystemConfig();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    saveSystemConfig(){
      this.$refs.uploadFile.clearFiles();
      let that = this;
      let _jsondata_ =   {
        sd_company_uuid:this.configForm.sd_company_uuid,
        sd_uuid:this.configForm.sd_uuid,
        sd_key:this.configForm.sd_key,
        sd_value:this.configForm.sd_value,
        sd_memo_2:this.configForm.sd_memo_2,
        sd_order:this.configForm.sd_order
      } ;
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });

      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postSaveSystemConfig,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){
          this.configDialog.configFormVisible1=false;
          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration:3000,
            dangerouslyUseHTMLString: true,
            // iconClass:'el-icon-info',
            message: '<spand style="color:black;"> 保存成功 </spand>'
          });
          this.querySystemConfigData();
        }else{
          console.info("保存失败");
          loading.close();


          this.$alert(res.data.msg, '保存失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                this.$router.replace({path:'/login'});
              }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        that.tableData = [];
        this.$alert(JSON.stringify(error.message), '保存出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        that.tableData = [];
        this.$alert(JSON.stringify(res.message), '保存出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },
    deleteSystemConfig(_sd_uuid_){

      let that = this;
      let _jsondata_ =   {
        sd_uuid:_sd_uuid_,
      } ;
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });

      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postDeleteSystemConfig,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){
          this.configDialog.configFormVisible1=false;
          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration:3000,
            dangerouslyUseHTMLString: true,
            // iconClass:'el-icon-info',
            message: '<spand style="color:black;"> 删除成功 </spand>'
          });
          this.querySystemConfigData();
        }else{
          console.info("删除失败");
          loading.close();


          this.$alert(res.data.msg, '删除失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                this.$router.replace({path:'/login'});
              }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        that.tableData = [];
        this.$alert(JSON.stringify(error.message), '删除出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        that.tableData = [];
        this.$alert(JSON.stringify(res.message), '删除出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },
    closeDialog1(){
      this.$refs.uploadFile.clearFiles();
      this.$refs.configForm.clearValidate();
      this.configDialog.configFormVisible1 = false;

    },
    handleEditRow(index, row) {
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );

      this.configDialog.configDialogTitle = '修改配置项['+row.sd_memo+']';
      this.configDialog.configFormVisible1 = true;
      this.configForm.sd_uuid = row.sd_uuid;
      this.configForm.sd_key = row.sd_key;
      this.configForm.sd_memo = row.sd_memo;
      //对logo进行处理
      if(row.sd_key==='logo_img' && row.sd_value!==undefined){
        this.uploadConfig.uploadImageUrl = this.customGlobal.serverUrl.orgBaseURI + "/fr/imgserver/" + row.sd_value;
      }
      if(row.sd_uuid===undefined){
        console.info("新sd_uuid"+this.customGlobal.genLogicNo("SYSVAR"));
        this.configForm.sd_uuid = this.customGlobal.genLogicNo("SYSVAR");
      }
      console.info("row.sd_memo_2:"+row.sd_memo_2)
      if(row.sd_memo_2===undefined){
        this.configForm.sd_memo_2 = "备注";
      }else{
        this.configForm.sd_memo_2 = row.sd_memo_2;
      }
      if(row.sd_company_uuid===undefined){
        this.configForm.sd_company_uuid = this.customGlobal.userInfoExt.cui_company_id;
      }else{
        this.configForm.sd_company_uuid = row.sd_company_uuid;
      }
      if(row.sd_value===undefined){
        this.configForm.sd_value = '';
      }else{
        this.configForm.sd_value = row.sd_value;
      }
      if(row.sd_order===undefined){
        this.configForm.sd_order = '0';
      }else{
        this.configForm.sd_order = row.sd_order;
      }




    },
    handleDeleteRow(index, row) {
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );
      let uuid = row.sd_uuid;

      this.$confirm('请再次确认是否要删除[<span style="color:red;">'+row.sd_memo+'</span>]该行记录?', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString:true,
        closeOnClickModal:false,
        closeOnPressEscape:false,

      }).then(() => {

        this.deleteSystemConfig(uuid);


        // this.$message({
        //   type: 'success',
        //   message: '删除成功!'
        // });
      }).catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: '已取消删除'
        // });
      });
    },

    querySystemConfigData (){

      let that = this;
      //console.info("form元素："+JSON.stringify( this.ruleForm ));
      let _jsondata_ =   {

        perPageSize:this.queryParam.pageSize,
        currentPage:this.queryParam.currentPage,
        sd_company_uuid:this.customGlobal.userInfoExt.cui_company_id,
      } ;
      // if(this.sortOrderCol!==''){
      //   _jsondata_.orderby = this.sortOrderCol;
      // }
      //console.info("json:"+JSON.stringify(_jsondata_));

      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });

      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQuerySystemConfig,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );

      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          that.tableData = res.data.rows;
          that.queryParam.totalRow = res.data.total;

        }else{
          console.info("查询失败");
          loading.close();

          that.tableData = [];
          this.$alert(res.data.msg, '查询失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                this.$router.replace({path:'/login'});
              }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        that.tableData = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        that.tableData = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.queryParam.pageSize = val;
      this.querySystemConfigData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.queryParam.currentPage = val ;
      this.querySystemConfigData();
    },
    selUploadFile(files, fileList){
      //console.log("files:", files);
      //console.log("fileList:", fileList);

      if(fileList.length>=1){
        console.info("已经选择了一个文件，先删除之前选择的文件然后再重新选择文件")
        this.$message({
          type: 'success',
          showClose: true,
          duration:3000,
          dangerouslyUseHTMLString: true,
          // iconClass:'el-icon-info',
          message: '<span style="color:black;"> 已经选择了一个文件，先删除之前选择的文件然后再重新选择文件 </span>'
        });
        //this.$refs.uploadFile.clearFiles();
      }
    },
    handleAvatarSuccess(res, file) {
      this.uploadConfig.uploadImgFlag = false;
      this.uploadConfig.uploadPercent=0;
      this.configForm.sd_value = res.rawfilename;
      //对logo进行处理
      this.uploadConfig.uploadImageUrl = this.customGlobal.serverUrl.orgBaseURI+ "/fr/imgserver/" + res.rawfilename;
      console.info("服务端返回信息："+JSON.stringify(res));
    },
    beforeAvatarUpload(file) {

      // let isJPG = file.type === 'image/jpeg';
      let isJPG = false;
      if (
        ['image/jpeg',
          // 'image/gif',
          'image/png',
          // 'image/bmp'
        ].indexOf(file.type) == -1) {
        isJPG = false;
      }else{
        isJPG = true;
      }

      const isLt2M = file.size / 1024 < 10;
      console.info(file.size+"文件大小："+(file.size / 10) );

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG|PNG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 10KB!');
      }
      return isJPG && isLt2M;
    },

  }
}
</script>

<style scoped>

</style>



